import React from "react";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faBolt, faClock } from '@fortawesome/free-solid-svg-icons'

const COLOR_MAP = {
    0: "bg-red-600",
    1: "bg-red-400",
    2: "bg-red-200",
    3: "bg-gray-200",
    4: "bg-green-200",
    5: "bg-green-400",
    6: "bg-green-500",
    7: "bg-green-600",
    8: "bg-green-700", 
    9: "bg-green-800",
    "?": "bg-purple-600"
}

export default function Todo(props) {
    function convDur(dur) {
        if (Number.isFinite(dur)) {
            if (dur < 0) {
                return "-" + new Date(dur*(-1) * 1000).toISOString().substr(11, 5)
            } else {
                return new Date(dur * 1000).toISOString().substr(11, 5)
            }
        } else {
            return "N/A"
        }
    }
    let NeutralDurStr = convDur(props.nh)
    let RawDurStr = convDur(props.dur)

    return (
            <div className="flex bg-gray-50 rounded-3xl shadow-md ml-1 mr-6 mt-3 mb-3 border-2 border-gray-50 max-w-xs">
                <div className={"flex flex-wrap h-auto text-center relative p-5 rounded-l-3xl content-evenly text-white font-black " + COLOR_MAP[props.rating]}>
                    <div className="text-4xl w-5">{props.rating}</div>
                </div>
                
                <div className="h-auto p-3 grid grid-rows-2 place-items-center h-40 w-64">
                    <div className="text-md font-light text-black max-w-sm px-1">{props.name}</div>
                    <div className="flex flex-auto text-gray-400 text-sm mt-2">
                        <div class="space-x-4 flex">
                            <div className="bg-gray-100 rounded-full px-2 py-1"><FontAwesomeIcon icon={faClock} /> {RawDurStr}</div>
                            <div className="bg-gray-100 rounded-full px-2 py-1"><FontAwesomeIcon icon={faBolt} /> {NeutralDurStr}</div>
                        </div>
                    </div>
                </div>
            </div>
    );
}
